.table {
    border: 1px none;
    height: 325px;
    width: 356px;
  }
  
  .table .overlap {
    height: 524px;
    left: 14px;
    position: relative;
    top: -251px;
    width: 328px;
  }
  
  .table .table-leg {
    height: 114px;
    left: 14px;
    position: absolute;
    top: 410px;
    width: 211px;
  }
  
  .table .line {
    height: 2px;
    left: 0;
    position: absolute;
    top: 0;
    width: 15px;
  }
  
  .table .img {
    height: 2px;
    left: 196px;
    position: absolute;
    top: 0;
    width: 15px;
  }
  
  .table .table-top {
    height: 413px;
    left: 0;
    position: absolute;
    top: 0;
    width: 328px;
  }
  
  .table .overlap-group {
    height: 415px;
    position: relative;
    top: -2px;
  }
  
  .table .line-2 {
    height: 413px;
    left: 0;
    position: absolute;
    top: 1px;
    width: 3px;
  }
  
  .table .line-3 {
    height: 413px;
    left: 325px;
    position: absolute;
    top: 1px;
    width: 3px;
  }
  
  .table .line-4 {
    height: 4px;
    left: 0;
    position: absolute;
    top: 411px;
    width: 328px;
  }
  
  .table .line-5 {
    height: 1px;
    left: 0;
    object-fit: cover;
    position: absolute;
    top: 379px;
    width: 328px;
  }
  
  .table .rectangle {
    background: linear-gradient(180deg, rgb(35, 35, 35) 0%, rgba(35, 35, 35, 0) 100%);
    height: 413px;
    left: 0;
    position: absolute;
    top: 0;
    width: 328px;
  }

  .table .table-top .player {
    margin-top: -90px;
    margin-left: 10px;
  }