.info-screen {
  background-color: #00000040;
  border: 1px none;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.info-screen .overlap-wrapper {
  background-color: #232323;
  border: 5px solid;
  border-color: #ffffff;
  border-radius: 15px;
  height: 305px;
  position: relative;
  width: 305px;
}

.info-screen .overlap {
  height: 265px;
  left: 12px;
  position: relative;
  top: 16px;
  width: 293px;
}

.info-screen .main-title {
  height: 260px;
  left: 0;
  position: absolute;
  top: 17px;
  width: 281px;
}

.info-screen .overlap-group {
  height: 265px;
  position: relative;
  top: -1px;
}

.info-screen .flexcontainer {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  gap: 11px;
  height: 264px;
  left: 1px;
  position: absolute;
  top: 1px;
  width: 280px;
}

.info-screen .text {
  align-self: stretch;
  color: #606060;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 24px;
  position: relative;
  text-align: center;
}

.info-screen .text-wrapper {
  color: #606060;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 24px;
}

.info-screen .div {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  gap: 11px;
  height: 264px;
  left: 0;
  position: absolute;
  top: 0;
  width: 280px;
}

.info-screen .span-wrapper {
  align-self: stretch;
  color: #ffffff;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 24px;
  position: relative;
  text-align: center;
}

.info-screen .span {
  color: #ffffff;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 24px;
}

.info-screen .quit {
  height: 39px;
  position: absolute;
  top: -12px;
  left: 250px;
  width: 50px;
}

.info-screen .overlap-2 {
  height: 28px;
  left: -47px;
  position: relative;
  top: -1px;
  width: 145px;
}

.info-screen .start-button-shadow {
  color: #606060;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 36px;
  font-weight: 500;
  height: 27px;
  left: 1px;
  letter-spacing: 0;
  line-height: 36px;
  position: absolute;
  text-align: center;
  top: 1px;
  white-space: nowrap;
  width: 144px;
}

.info-screen .start-button {
  color: #ffffff;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 36px;
  font-weight: 500;
  height: 27px;
  left: 0;
  letter-spacing: 0;
  line-height: 36px;
  position: absolute;
  text-align: center;
  top: 0;
  white-space: nowrap;
  width: 144px;
}

.top-margined {
  line-height: 48px !important;
}
.small-wrapper {
  line-height: 16px !important;
  font-size: 16px !important;
}
.small-wrapper>.span {
  line-height: 16px !important;
  font-size: 16px !important;
}