.players {
    border: 1px none;
    height: 61px;
    width: 24px;
  }
  
  .players .player {
    height: 61px;
  }
  
  .players .overlap-group {
    height: 72px;
    left: -9px;
    position: relative;
    top: -1px;
    width: 44px;
  }
  
  .players .div {
    background-color: #ffffff;
    border-radius: 14px;
    box-shadow: 0px 0px 8px #000000;
    height: 28px;
    left: 7px;
    position: absolute;
    top: 36px;
    width: 28px;
  }
  
  .players .player-2 {
    background-color: #2389ad;
    border-radius: 12px;
    height: 24px;
    left: 9px;
    position: absolute;
    top: 38px;
    width: 24px;
  }
  
  /* .players .arrow {
    height: 40px;
    left: 14px;
    position: absolute;
    top: 0;
    width: 15px;
  } */
  
  .players .text-wrapper {
    color: #606060;
    font-family: "DOSMyungjo-Medium", Helvetica;
    font-size: 16px;
    font-weight: 500;
    height: 43px;
    left: 1px;
    letter-spacing: 0;
    line-height: 16px;
    position: absolute;
    text-align: center;
    top: 43px;
    width: 43px;
  }
  
  .players .text-wrapper-2 {
    color: #ffffff;
    font-family: "DOSMyungjo-Medium", Helvetica;
    font-size: 16px;
    font-weight: 500;
    height: 43px;
    left: 0;
    letter-spacing: 0;
    line-height: 16px;
    position: absolute;
    text-align: center;
    top: 42px;
    width: 43px;
  }
  