.game-container {
  width: 100vw;
  height: 100vh;
  background: #232323;
}
.game-container canvas {
  position: absolute;
  top: 0;
  left: 0;
}
.game-container .gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80%;
  z-index: 0;
  background: linear-gradient(180deg, #232323 0%, rgba(35, 35, 35, 0) 100%);
}
.game-container .top-bar {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: row;

  width: calc(100% - 26px);
  height: fit-content;
  padding: 8 13 0;
  justify-content: space-between;
}
.game-container .left-corner-title {
  color: #ffffff;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 16px;
  font-weight: 500;
  height: 19px;
  left: 0;
  letter-spacing: 0;
  line-height: 16px;
  top: 0;
  white-space: nowrap;
  width: 104px;
}

.game-container .right-corner-title {
  color: #ffffff;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 16px;
  font-weight: 500;
  height: 19px;
  left: 0;
  letter-spacing: 0;
  line-height: 16px;
  text-align: right;
  top: 0;
  white-space: nowrap;
  width: 104px;
}
.game-container .ranking {
  position: absolute;
  z-index: 2;
  top: 48px;
  right: 46px;
  display: flex;
  width: 144px;
  flex-direction: column;
}
.game-container .big-ranking {
  position: absolute;
  z-index: 2;
  top: 99px;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.game-container .rank {
  color: #fff;
  text-align: right;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%; /* 24px */
}
.game-container .big-rank {
  justify-content: center;
  color: #fff;
  text-align: center;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 54px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%; /* 54px */
}
.game-container .time-left {
  position: absolute;
  width: 100%;
  z-index: 2;
  bottom: 22px;
  color: #fff;
  text-align: center;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%; /* 24px */
}
.game-container .countdown {
  position: relative;
  z-index: 2;
  color: #ffffff;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-weight: 500;
  height: 52px;
  margin: 117px auto 0;
  letter-spacing: 0;
  line-height: 32px;
  text-align: center;
  width: 320px;
}
.game-container .replay-button {
  position: absolute;
  z-index: 2;
  width: 100%;
  align-items: center;
  bottom: 79px;
}

.game-container .replay {
  color: #fff;
  text-align: center;
  font-family: "DOSMyungjo-Medium", Helvetica;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%; /* 30px */
  margin-bottom: 18px;
}

.game-container .player-rank {
  display: flex;
  flex-direction: row;
  color: #fff;
  width: 100%;
  height: 60px;
  justify-content: center;
}

.game-container .player-icon {
  border-radius: 25px;
  height: 50px;
  width: 50px;
  margin: 5px;
}

.game-container .player-number {
  color: #fff;
  text-align: center;
  font-family: "DOSMyungjo-Medium", Helvetica;
  margin: 7px 0;
  font-size: 36px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%; /* 43px */
}
